// Burger
menuExpanded = (el) => {
    el.classList.toggle("menu-expanded");
};

//Modal
document.addEventListener("DOMContentLoaded", function() {
    try {
        MicroModal.init({
            awaitCloseAnimation: true,// set to false, to remove close animation
            onShow: function(modal) {
                setTimeout(() => {
                    disableInputFocus();
                }, 100)
            },
            onClose: function(modal) {
            }
        });
    } catch (e) {
        console.log("micromodal error: ", e);
    }
});

// Disable autofocus
const disableInputFocus = () => {
    const elelist = document.getElementsByTagName("input");
    for(var i = 0; i < elelist.length; i++){
        elelist[i].addEventListener("focus", function(){
            this.blur();
        });
    }
}

// Tabs Init
function Tabs() {
    var bindAll = function() {
        var menuElements = document.querySelectorAll('[data-tab]');
        for(var i = 0; i < menuElements.length ; i++) {
            menuElements[i].addEventListener('click', change, false);
        }
    }

    var clear = function() {
        var menuElements = document.querySelectorAll('[data-tab]');
        for(var i = 0; i < menuElements.length ; i++) {
            menuElements[i].classList.remove('active');
            menuElements[i].querySelector('.heading__text').classList.remove('active');
            var id = menuElements[i].getAttribute('data-tab');
            document.getElementById(id).classList.remove('active');
        }
    }

    var change = function(e) {
        clear();
        e.target.classList.add('active');
        var id = e.currentTarget.getAttribute('data-tab');
        document.getElementById(id).classList.add('active');
    }

    bindAll();
}

var connectTabs = new Tabs();

// Vanilla carousel
const carouselSlide = document.querySelector('.carousel-slide');
const carouselData = document.querySelectorAll('.carousel-slide img, .carousel-slide .video');
//Buttons
const previousBtn = document.querySelector('#previousBtn');
const nextBtn = document.querySelector('#nextBtn');
//Counter
let counter = 0;
const size = carouselData[0].clientWidth;
// set current iframe width
const iframes = document.querySelectorAll("iframe");
for( var i = 0; i < iframes.length; i++) {
    iframes[i].width = size;
}

carouselSlide.style.transform = 'translateX(' + (-size * counter) + 'px)';

nextBtn.addEventListener('click', () => {
    if (counter >= carouselData.length - 3) return;
    carouselSlide.style.transition = 'transform 0.2s ease-in-out';
    counter++;
    carouselSlide.style.transform = 'translateX(' + (-size * counter) + 'px)';
});

previousBtn.addEventListener('click', () => {
    if (counter <= 0) return;
    carouselSlide.style.transition = 'transform 0.2s ease-in-out';
    counter--;
    carouselSlide.style.transform = 'translateX(' + (-size * counter) + 'px)';
});


carouselSlide.addEventListener('transitionend', () => {
    if (carouselData[counter].id === 'firstClone') {
        carouselSlide.style.transition = 'none';
        counter = carouselData.length - counter;
        carouselSlide.style.transform = 'translateX(' + (-size * counter) + 'px)';

    }
});
